@import "../resources/variables.scss";

.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(240, 248, 255, 0.5);
}

.hidden {
  display: none;
}

.loader {
  position: absolute;
}
